input[type="range"] {
  -webkit-appearance: none;
  border-radius: 5px;
  outline: none;
  padding: 0;
  margin: 0;
  height: 5px;
  min-width: 200px;
  background-color: #ccc;
  overflow: hidden;
}

input[type="range"]::-webkit-slider-thumb {
  width: 10px;
  -webkit-appearance: none;
  height: 5px;
  cursor: ew-resize;
  background: #434343;
  box-shadow: -999px 0 0 999px #43e5f7;
}

.range-slider {
  display: flex;
  align-items: center;
  margin: 1px 0;
}

.range-slider__title {
  min-width: 65px;
  font-size: 9pt;
  margin-right: 0.5rem;
  font-weight: bold;
}

.range-slider__value {
  margin-left: 0.5rem;
  font-size: 9pt;
}
